import { String } from "../../Model/String";
import { Injectable } from "@angular/core";
import { ServicesProvider } from "../services/services";
import { FileExportBlobListResponse } from "../../Model/Response/FileExportBlobListResponse";
import { FileExportBlobResponse } from "../../Model/Response/FileExportBlobResponse";
import { FileExportBlobViewModel } from '../../Model/ViewModel/FileExportBlobViewModel';
import { FileServicesProvider } from '../file-services/file-services';

/*
  Generated class for the FileExportBlobServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/

@Injectable()
export class FileExportBlobServicesProvider extends FileServicesProvider {
  private FileExportBlobs: FileExportBlobListResponse;
  private FileExportBlob: FileExportBlobResponse;
   ctl: string;

  constructor(
  public Services: ServicesProvider
  ) {
    super(Services);
    this.ctl = "FileExportBlob";
    console.log("Hello FileExportBlobServiceProvider Provider");
  }

  public GetListsByAsync(PageSize: number, PageNumber: number, keyword: string,exec_group:string,exec_file_seq:number,order_type:boolean) {
    var sub_url: string;
    if(exec_file_seq===0)
    {
      sub_url = "FileExportBlob?PageSize={1}&PageNumber={2}&keyword={3}&exec_group={4}&order_type={5}";
      sub_url = String.Format(sub_url, this.ctl, PageSize, PageNumber, keyword,exec_group,order_type);
    }
    else
    {
      sub_url = "FileExportBlob/Select?PageSize={1}&PageNumber={2}&keyword={3}&exec_group={4}&exec_file_seq={5}&order_type={6}";
      sub_url = String.Format(sub_url, this.ctl, PageSize, PageNumber, keyword,exec_group,exec_file_seq,order_type);
    }
    return this.Services.GetAsync(sub_url,this.ctl,true).map(item => {
      this.FileExportBlobs = new FileExportBlobListResponse({
        Message: item.Message,
        DidError: item.DidError,
        ErrorMessage: item.ErrorMessage,
        PageSize: item.PageSize,
        PageNumber: item.PageNumber,
        TotalRows: item.TotalRows,
        PageRows: item.PageRows,
        TotalPages: item.TotalPages,
        Model: item.Model
      });
      return this.FileExportBlobs;
    });
  }



  public PostPutAsync(FileExportBlobViewModel:FileExportBlobViewModel,mode:string) {
    var sub_url: string;
    if(mode==="POST")
      sub_url = "FileExportBlob";
    else
    sub_url = "FileExportBlob/{1}";
    sub_url = String.Format(sub_url, this.ctl,FileExportBlobViewModel.exec_file_seq);
    return this.Services.PostPutAsync(sub_url, this.ctl, true,FileExportBlobViewModel,mode).map(
      item => {
        this.FileExportBlob = new FileExportBlobResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
        return this.FileExportBlob;
      }
    );
  }
}



