export class FileExportBlobViewModel {
  /// <summary>
  ///  轉檔序號
  /// <summary>
  public exec_file_seq: number;
  /// <summary>
  ///  轉檔群組
  /// <summary>
  public exec_group: string;

  /// <summary>
  ///  啟用註記
  /// <summary>
  public is_active: boolean;

  /// <summary>
  ///  執行順序
  /// <summary>
  public exec_seq: number;
  /// <summary>
  ///  重複執行註記
  /// <summary>
  public can_rerun: boolean;
  /// <summary>
  ///  參數欄位群組
  /// <summary>
  public parameter_column_group: string;
  /// <summary>
  ///  建立者
  /// <summary>
  public creator: string;
  /// <summary>
  ///  建立時間
  /// <summary>
  public create_time: Date;
  /// <summary>
  ///  最後更新者
  /// <summary>
  public modifier: string;
  /// <summary>
  ///  最後更新時間
  /// <summary>
  public last_update_time: Date;
  /// <summary>
  ///  備註
  /// <summary>
  public note: string;

  /// <summary>
  ///  執行失敗停止
  /// </summary>
  public is_fail_stop: boolean;

  /// <summary>
  ///  匯出檔案路徑主鍵
  /// </summary>

  public out_file_path_key: string;

  /// <summary>
  ///  匯出檔案路徑
  /// </summary>

  public out_file_path: string;



  /// <summary>
  ///  啟用註記說明
  /// <summary>
  public is_active_desc: string;
  /// <summary>
  ///  啟用註記顏色
  /// <summary>
  public is_active_color: string;

  /// <summary>
///  啟用顏色
/// <summary>
public is_active_color_right: string;

  /// <summary>
  ///  結構描述
  /// <summary>
  public schemaname: string;
  /// <summary>
  ///  資料表
  /// <summary>
  public tablename: string;
  /// <summary>
  ///  db連線編號
  /// <summary>
  public db_conn_id: string;



  /// <summary>
  /// Sql語法
  /// <summary>
  public sql_statement: string;


  /// <summary>
  ///  資料表全名
  /// <summary>
  public tablefullname: string;
  /// <summary>
  ///  db連線字串
  /// <summary>
  public db_conn_string: string;

  /// <summary>
  ///  資料來源匯出類型主鍵
  /// <summary>
  public src_export_type_key: string;
  /// <summary>
  ///  資料來源匯出類型
  /// <summary>
  public src_export_type: string;

  /// <summary>
  ///  是否有設定ProcessedExecGroup
  /// </summary>
  public is_processed_exec_group: boolean;

  /// <summary>
  ///  ProcessedExecGroup顏色
  /// </summary>
  public processed_exec_group_color: string;
}



