import { FileExportBlobServicesProvider } from "../../providers/file-export-blob-services/file-export-blob-services";
import { GlobalComponent } from "../../components/global/global";
import { ModalController, LoadingController } from "ionic-angular";
import { Component } from "@angular/core";
import { String } from "../../Model/String";
import {
  IonicPage,
  NavController,
  NavParams,
  ViewController
} from "ionic-angular";
import { FileExportBlobViewModel } from "../../Model/ViewModel/FileExportBlobViewModel";
import { FilePathServicesProvider } from "../../providers/file-path-services/file-path-services";
import { FilePathViewModel } from "../../Model/ViewModel/FilePathViewModel";
import { ConnServicesProvider } from "../../providers/conn-services/conn-services";

/**
 * Generated class for the FileExportBlobModalPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: "page-file-export-blob-add-edit-modal",
  templateUrl: "file-export-blob-add-edit-modal.html"
})
export class FileExportBlobAddEditModalPage {
  func_no: number = 3;
  func_key: string = "014|" + this.func_no;
  item: FileExportBlobViewModel;
  default_path: FilePathViewModel[];
  src_file_path: FilePathViewModel;
  err_file_path: FilePathViewModel;
  bk_file_path: FilePathViewModel;
  mode: string;
  CanEditBatch: boolean;
  CanFileBrowser: boolean;
  title: string;
  FileExportBlob_type: string;
  min_exec_file_seq: number = 37000;
  max_exec_file_seq: number = this.min_exec_file_seq + 999;
  range_exec_file_seq: string;
  change_mode: string;
  sql_help: string = `
  --sql自訂語法中，系統會取前兩個欄位進行匯入，第一欄位為檔案名稱，第二欄位為二進位資料(例如：varbinary(max)型態)
  --以下為範例sql 語法
  SELECT[filename] --檔案名稱
      ,[binarydata] --二進位資料
  FROM [comm].[tb_file_content_log]
  `;
  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public viewCtrl: ViewController,
    private FileExportBlobServices: FileExportBlobServicesProvider,
    private FilePathServices: FilePathServicesProvider,
    public loadingCtrl: LoadingController,
    private global: GlobalComponent,
    public modalCtrl: ModalController,
    private ConnServices: ConnServicesProvider,
  ) {
    this.range_exec_file_seq = String.Format(
      "{0}-{1}",
      this.min_exec_file_seq,
      this.max_exec_file_seq
    );
    this.mode = navParams.data.mode;
    this.change_mode = navParams.data.change_mode;
    this.item = new FileExportBlobViewModel();
    this.item.exec_file_seq = navParams.data.item.exec_file_seq;
    this.item.exec_group = navParams.data.item.exec_group;
    this.item.is_active = navParams.data.item.is_active;
    this.item.exec_seq = navParams.data.item.exec_seq;
    this.item.can_rerun = navParams.data.item.can_rerun;
    this.item.parameter_column_group =
      navParams.data.item.parameter_column_group;
    this.item.creator = navParams.data.item.creator;
    this.item.create_time = navParams.data.item.create_time;
    this.item.modifier = navParams.data.item.modifier;
    this.item.last_update_time = navParams.data.item.last_update_time;
    this.item.note = navParams.data.item.note;
    this.item.is_fail_stop = navParams.data.item.is_fail_stop;
    this.item.out_file_path_key = navParams.data.item.out_file_path_key;
    this.item.out_file_path = navParams.data.item.out_file_path;
     this.item.schemaname = navParams.data.item.schemaname;
    this.item.tablename = navParams.data.item.tablename;
    this.item.tablefullname = navParams.data.item.tablefullname;
    this.item.db_conn_id = navParams.data.item.db_conn_id;
    this.item.db_conn_string = navParams.data.item.db_conn_string;
    this.item.parameter_column_group =
      navParams.data.item.parameter_column_group;
    this.item.sql_statement = navParams.data.item.sql_statement;

    if (this.change_mode === "n") {
      this.get_default_path();
      this.get_conn_string("localdb");
    }
    if (this.change_mode === "c") {
      this.get_max_exec_file_seq();
      this.get_max_exec_seq();
    }
    this.CanEditBatch = navParams.data.CanEditBatch;
    this.CanFileBrowser = navParams.data.CanFileBrowser;
    this.title = "Blob欄位匯出";
  }
  Help() {
    this.global.createLoader();

    this.global.loading.present().then(() => {
      this.global.showMessage("SQL語法說明", this.sql_help);
    });
  }
  openNavPage(parent_path) {
    let page;
    if(parent_path.indexOf("\\")>=0)
      page="DirectoryBrowserPage";
    if(parent_path.indexOf("/")>=0)
      page="FtpDirectoryBrowserPage";
    this.global.createLoader("連線中...");
    this.global.loading.present().then(() => {

      this.navCtrl.push(page, { parent_path: parent_path,parent_page:this,is_modal:true});
    });
    this.global.dismissLoading();
  }
  openNavParameterColumnPage(item) {
    this.global.createLoader("連線中...");
    this.global.loading.present().then(() => {
      this.navCtrl.push("ParameterColumnPage", { item: item });
    });
    this.global.dismissLoading();
  }
  get_conn_string(remote_conn_id: string) {
    this.global.createLoader("取得連線字串中...");
    this.global.loading.present().then(() => {
      this.ConnServices.GetConnAsync(remote_conn_id).subscribe(
        data => {
          if (data.DidError === true) {
            this.global.dismissLoading();
            this.global.showError(data.ErrorMessage);
          } else {
            this.item.db_conn_string =
              data.Model.conn_string;
            this.global.dismissLoading();
          }
        },
        err => {
          this.global.dismissLoading();
          this.global.showError("無法連上WebAPI伺服器-"+err.message);
        }
      );
    });
  }


  SelectConn() {
    let modal = this.modalCtrl.create("ConnSelectModalPage", {
      select_remote_conn_id: this.item.db_conn_id,
      conn_type_key: "065|001"
    });
    modal.onDidDismiss(select_data => {
      if (select_data == null) return;
      this.item.db_conn_id = select_data.remote_conn_id;
      this.item.db_conn_string = select_data.conn_string;
    });

    modal.present();
  }
  SelectParameterGroup() {
    let modal = this.modalCtrl.create("ParameterGroupSelectModelPage", {
      select_parameter_column_group: this.item.parameter_column_group
    });
    modal.onDidDismiss(select_data => {
      if (select_data == null) return;
      this.item.parameter_column_group = select_data.parameter_column_group;
    });

    modal.present();
  }

  SelectTable() {
    let modal = this.modalCtrl.create("TableExtSelectModalPage", {
      select_item: this.item.tablefullname
    });
    modal.onDidDismiss(select_data => {
      if (select_data == null) return;
      this.item.schemaname = select_data.schemaname;
      this.item.tablename = select_data.tablename;
      this.item.tablefullname = select_data.tablefullname;
    });

    modal.present();
  }
  SelectOutFilePath() {
    let modal = this.modalCtrl.create("CodeSelectModalPage", {
      select_key: this.item.out_file_path_key,
      code_type: "005"
    });
    modal.onDidDismiss(select_data => {
      if (select_data == null) return;
      this.item.out_file_path_key = select_data.code_key;
      this.item.out_file_path = select_data.code_desc;
    });

    modal.present();
  }

  get_max_exec_file_seq() {
    this.global.createLoader("取得最大值中...");
    this.global.loading.present().then(() => {
      this.FileExportBlobServices.GetMaxRangeExecFileSeqAsync(
        this.func_no,
        this.item.exec_group,
        this.min_exec_file_seq,
        this.max_exec_file_seq,
      ).subscribe(
        data => {
          if (data.DidError === true) {
            this.global.dismissLoading();
            this.global.showError(data.ErrorMessage);
          } else {
            this.item.exec_file_seq = data.Model;
            this.global.dismissLoading();
          }
        },
        err => {
          this.global.dismissLoading();
          this.global.showError("無法連上WebAPI伺服器-"+err.message);
        }
      );
    });
  }
  get_default_path() {
    this.global.createLoader("取得預設資料夾中...");
    this.global.loading.present().then(() => {
      this.FilePathServices.GetDefaultListsByAsync(this.func_key).subscribe(
        data => {
          if (data.DidError === true) {
            this.global.dismissLoading();
            this.global.showError(data.ErrorMessage);
          } else {
            this.default_path = data.Model;
            this.src_file_path = this.default_path.filter(
              (item, FilePathViewModel) => item.file_path_type_key === "004|OUT"
            )[0];
            this.item.out_file_path_key = this.src_file_path.file_path_key;
            this.item.out_file_path = this.src_file_path.file_path;

            this.global.dismissLoading();
          }
        },
        err => {
          this.global.dismissLoading();
          this.global.showError("無法連上WebAPI伺服器-"+err.message);
        }
      );
    });
  }
  get_max_exec_seq() {
    this.global.createLoader("取得最大值中...");
    this.global.loading.present().then(() => {
      this.FileExportBlobServices.GetMaxExecSeqAsync(
        this.func_no,
        this.item.exec_group
      ).subscribe(
        data => {
          if (data.DidError === true) {
            this.global.dismissLoading();
            this.global.showError(data.ErrorMessage);
          } else {
            this.item.exec_seq = data.Model;
            this.global.dismissLoading();
          }
        },
        err => {
          this.global.dismissLoading();
          this.global.showError("無法連上WebAPI伺服器-"+err.message);
        }
      );
    });
  }

  Save() {
    this.viewCtrl.dismiss(this.item);
  }
  close() {
    this.viewCtrl.dismiss();
  }
  ionViewDidLoad() {
    console.log("ionViewDidLoad FileExportBlobModalPage");
  }
}



